<template>
  <div class="home">
    <div class="login">
      <el-form ref="formRef" :model="form" :rules="rules" style="width: 100%;" label-position="top">
        <el-form-item v-if="type === 'login'" label="账号/手机号" prop="user">
          <el-input v-model="form.user" clearable placeholder="请输入账号/手机号" />
        </el-form-item>
        <el-form-item v-if="type === 'login'" style="margin-bottom: 30px;" label="密码" prop="pass">
          <el-input v-model="form.pass" type="password" clearable placeholder="请输入密码" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" label="账号" prop="rUser">
          <el-input v-model="form.rUser" clearable placeholder="请输入账号" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" label="密码" prop="rPass">
          <el-input v-model="form.rPass" clearable placeholder="请输入密码" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" label="昵称" prop="rName">
          <el-input v-model="form.rName" clearable placeholder="请输入昵称" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" label="邮箱" prop="rEmail">
          <el-input v-model="form.rEmail" clearable placeholder="请输入邮箱" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" label="角色" prop="rRole">
          <el-select style="width: 100%;" v-model="form.rRole" clearable placeholder="请选择角色">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item v-if="type !== 'login'" style="margin-bottom: 30px;" label="手机号" prop="rPhone">
          <el-input v-model="form.rPhone" clearable placeholder="请输入手机号" />
        </el-form-item>
        <el-button style="width: 100%; margin-bottom: 10px;" type="primary" @click="loginHandle">{{ type === 'login' ? '登录' : '注册' }}</el-button>
        <div class="registry">
          <el-link type="primary" @click="changeHandle"> {{ type === 'login' ? '注册账号' : '返回登录' }}</el-link>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from 'vue'
import {  useRouter} from 'vue-router'
import { ElMessage } from 'element-plus'
import { rules, options } from './config'
export default {
  setup() {
    const router = useRouter()
    const formRef = ref()
    const type = ref('login')
    const form = reactive({
      user: '',
      pass: '',
      rUser: '',
      rPass: '',
      rName: '',
      rEmail: '',
      rRole: '',
      rPhone: ''
    })
    const changeHandle = () => {
      type.value = type.value === 'login' ? 'registry' : 'login'
      formRef.value && formRef.value.resetFields()
    }
    const loginHandle = () => {
      formRef.value && formRef.value.validate((val) => {
        if (val) {
          if (type.value === 'login') {
            ElMessage.success('登录成功')
            router.push({  name: 'main'})
          } else {
            ElMessage.success('注册成功')
            type.value = 'login'
          }
        }
      })
    }

    return {
      form,
      rules,
      formRef,
      type,
      options,
      changeHandle,
      loginHandle
    }
  },
}
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
